.header {
  width: 100%;
  height: 70px;
  background-color: #664596;
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
  margin: 0;
  top: 0;
  left: 0;
}

.home {
  width: 25%;
  height: 25%;
  margin: auto;
  display: block;
}

.phone-header {
  width: 50%;
  font-size: 12px;
  color: #ECEDD0;
}

.main-home {
  display: none;
  height: 60%;
  width: 40%;
  background-color: #ECEDD0;
  color: #664596;
}

.burger {
  position: relative;
}

.menu {
  position: absolute;
  left: -100;
  transition: left ease 5s;
}

.hidden {
  display: none;
}

.new-position {
  left: 50%;
}

.button {
  background-color: #2AB24A;
  color: white;
  padding: 6px 12px;
  margin-bottom: 0;
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 8px;
}

.button:hover {
  background-color: #23923D;
  font-weight: bold;
}

#button-menu {
  cursor: pointer;
  padding: 10px 35px 16px 0px;
  width: 25%;
  height: 25%;
  margin-left: 20px;
  z-index: 1;
}

#button-menu span,#button-menu span:before,#button-menu span:after {
  cursor: pointer;
  border-radius: 1px;
  height: 4px;
  width: 30px;
  background: #ECEDD0;
  position: absolute;
  display: block;
  content: '';
}

#button-menu span:before {
  top: -10px;
}

#button-menu span:after {
  bottom: -10px;
}

#button-menu span,#button-menu span:before,#button-menu span:after {
  transition: all 500ms ease-in-out;
}

#button-menu.active span {
  background-color: transparent;
}

#button-menu.active span:before,#button-menu.active span:after {
  top: 0;
}

#button-menu.active span:before {
  transform: rotate(45deg);
}

#button-menu.active span:after {
  transform: rotate(-45deg);
}

.container--tips {
  border: 4px double #664596;
