}

.link:hover {
  font-weight: bold;
  color: #2AB24A;
}

.burger {
  position: fixed;
  left: 0;
  top: -4%;
  height: 100vh;
  background-color: transparent;
  transition: transform ease .7s;
  transform: translateX(-280%);
}

.menu-visible .burger {
  transform: translateX(0);
  width: 100%;
  background-color: transparent;
}

.container-menu {
  position: relative;
