.resource .mask {
  opacity: 0;
  overflow: visible;
  border: 0px solid rgba(0, 0, 0, 0.7);
  box-sizing: border-box;
  transition: all 0.4s ease-in-out;
}

.view {
  width: 300px;
  height: 200px;
  margin: 10px;
  float: left;
  border-radius: 45px;
  overflow: hidden;
  position: relative;
  text-align: center;
  cursor: default;
}

.view .mask {
  width: 300px;
  height: 200px;
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
}

.view .logo-resources {
  width: 300px;
  height: 200px;
  display: block;
  position: relative;
}

.view a.info {
  background: url(../images/eye-button.png) center no-repeat;
  display: inline-block;
  text-decoration: none;
  padding: 0;
  text-indent: -9999px;
  width: 50px;
  height: 50px;
}

.resource a.info {
  position: relative;
  top: -110px;
  opacity: 0;
  transform: scale(0, 0);
  transition: transform 0.2s 0.1s ease-in, opacity 0.1s ease-in-out;
}

.resource:hover .mask {
  opacity: 1;
  border: 100px solid rgba(0, 0, 0, 0.7);
}

.resource:hover a.info {
  opacity: 1;
  transform: scale(1, 1);
  transition-delay: 0.3s;
}
